.hpbx-initialize-variables-404(@styleguide) when (@styleguide = 'upc-business') {
    @hpbxColor404BackgroundStart: #1ab3df;
    @hpbxColor404BackgroundEnd: #1ab3df;
    @hpbxColor404HeaderMenuOpenIcon: @sgColorPurple1;
    @hpbxColor404HeaderMenuOpenIconSM: @hpbxColorPrimary;
    @hpbxImageName404HeaderLogo: "";

}

.hpbx-initialize-variables-404(@styleguide) when (@styleguide = 'unitymedia') {
    .hpbx-initialize-variables-404('upc-business');
}

.hpbx-initialize-variables-404(@styleguide) when (@styleguide = 'ziggo') {
    @hpbxColor404BackgroundStart: #f6981a;
    @hpbxColor404BackgroundEnd: #f6981a;
    @hpbxColor404HeaderMenuOpenIcon: @sgColorGrey1;
    @hpbxColor404HeaderMenuOpenIconSM: @hpbxColorPrimary;
    @hpbxImageName404HeaderLogo: "-dark";
}

.hpbx-initialize-variables-404(@styleguide) when (@styleguide = 'virgin') {
    .hpbx-initialize-variables-404('ziggo');

    @hpbxColor404BackgroundStart: @sgColorRed;
    @hpbxColor404BackgroundEnd: @sgColorPinkHover;
    @hpbxColor404HeaderMenuOpenIcon: @sgColorPurple;
    @hpbxColor404HeaderMenuOpenIconSM: @sgColorRed;
    @hpbxImageName404HeaderLogo: "-dark";
}

.hpbx-initialize-variables-404(@styleguide) when (@styleguide = 'vtr') {
    @hpbxColor404BackgroundStart: #f6981a;
    @hpbxColor404BackgroundEnd: #f6981a;
    @hpbxColor404HeaderMenuOpenIcon: @sgColorGrey1;
    @hpbxColor404HeaderMenuOpenIconSM: @hpbxColorPrimary;
    @hpbxImageName404HeaderLogo: "-dark";
}

.hpbx-initialize-variables-404(@hpbxStyleguide);

body.hpbx-page-404 {
    .hpbx-body-background-color {
        .directional(@hpbxColor404BackgroundStart, @hpbxColor404BackgroundEnd, 45deg);
    }

    .hpbx-body-background-image,
    .hpbx-header-background-image {
        display: none !important;
    }

    .hpbx-header {
        background-color: transparent;
        border: none;
    }

    .hpbx-header-logo {
        background: url("../../dist/img/company-logo-small-@{hpbxStyleguide}@{hpbxImageNameWizardHeaderLogo}.png") no-repeat;
        background-size: contain;
    }

    .hpbx-menu-handle-open {
        &:before {
            color: @hpbxColor404HeaderMenuOpenIcon;
        }
    }
}

.hpbx-404 {
    color: @sgColorGrey1;
    font-family: @sgFontFamilyHeadings;
    height: 100%;
    left: 0;
    line-height: 1;
    text-align: center;
    top: 0;
    width: 100%;

    .remMarginTop(-4.4);
}

.hpbx-404-image {
    background: url("../../dist/img/404-@{hpbxStyleguide}.png") no-repeat center center;
    background-size: contain;

    .remHeight(65.9);
}

.hpbx-404-message-title {
    .remFontSize(4);
}

.hpbx-404-message-text {
    .remFontSize(2.4);
    .remMarginTop(0.4);
}

@media @XS {
    .hpbx-404-xs();
}

.hpbx-404-xs() {
    body.hpbx-page-404 {
        .hpbx-menu-handle-open {
            &:before {
                color: @hpbxColor404HeaderMenuOpenIconSM;
            }
        }
    }
}

@media @SM {
    .hpbx-404-sm();
}

.hpbx-404-sm() {
    body.hpbx-page-404 {
        .hpbx-header-logo {
            background-image: url("../../dist/img/company-logo-big-@{hpbxStyleguide}@{hpbxImageNameWizardHeaderLogo}.png");
        }
    }

    .hpbx-404 {
        .remMarginTop(-12.4);
    }
}

.ie8 {
    .hpbx-404-sm();
}
