.chzn-results {
    .box-shadow(0 2px 2px rgba(0,0,0,.15));
}

.chzn-container {
    .chzn-single {
        .remPaddingRight(5);

        .sg-handle {
            .sg-divider {
                background-color: @sgColorSelectBackground !important;
                height: 100% !important;
                margin: 0 !important;

                &:after {
                    content: '\e62a';
                    font-family: 'LGI-Iconfont';

                    .remFontSize(1.6);
                    .remRight(1.0);
                }
            }
        }

        &:hover {
            color: @sgColorSelectTextHover;
        }
    }

    &.chzn-disabled {
        .chzn-single {
            border-color: @sgColorSelectBorderDisabled;
            color: @sgColorSelectTextDisabled;

            .sg-divider {
                background-color: @sgColorSelectBorderDisabled !important;
            }
        }
    }

    .chzn-drop {
        .chzn-results {
            .active-result {
                .remPaddingBottom(0.9);
                .remPaddingTop(0.9);
            }
        }

        .chzn-search input {
            .remPaddingBottom(0.9);
            .remPaddingTop(0.9);
        }

        &.sg-animate-down {
            .remMarginTop(0);
        }
    }
}