.sg-btn {
    border: 0;
    position: relative;

    .remMinHeight(@sgMiscHeightElements);
    .transition(0s);

     &.sg-btn-small {
        .sg-btn-small-virgin();
    }

    &.sg-pink {
        .sg-btn-virgin-pink();
    }

    &.sg-disabled {
        .sg-btn-virgin-disabled()
    }

    &.sg-purple {
        .sg-btn-virgin-purple()
    }

    &.sg-grey {
        .sg-btn-virgin-grey()
    }

    &.sg-without-icon {
        .remPaddingRight(@sgPaddingButtonLeft);
    }
}

.sg-btn-small-virgin() {
    line-height: 0;

    .remFontSize(1.4);
    .remMaxHeight(3);
    .remMinHeight(3);
    .remMinWidth(9);
    .remPadding(@sgPaddingButtonSmallTop, @sgPaddingButtonSmallRight, @sgPaddingButtonSmallBottom, @sgPaddingButtonSmallLeft);

    &.sg-without-icon {
        .remPaddingRight(@sgPaddingButtonSmallLeft);
    }
    
    &+ .sg-btn-icon {
        &:before {
            .remFontSize(1.1);
            .remLeft(-1.6);
            .remTop(0.3);
        }
    }
}

button.sg-btn.sg-btn-small {
    .remPaddingTop(0.2);
}

.sg-btn-virgin-pink() {
    .sg-give-horizontal-gradient-to-element(@sgColorButtonPinkStart, @sgColorButtonPinkEnd, @sgColorButtonPinkHoverStart, @sgColorButtonPinkHoverEnd, @sgColorButtonPinkText, @sgColorButtonPinkText, transparent, transparent);
}

.sg-btn-virgin-purple() {
    .sg-give-horizontal-gradient-to-element(@sgColorButtonPurpleStart, @sgColorButtonPurpleEnd, @sgColorButtonPurpleHoverStart, @sgColorButtonPurpleHoverEnd, @sgColorButtonPurpleText, @sgColorButtonPurpleText, transparent, transparent);
}

.sg-btn-virgin-grey() {
    .sg-give-color-to-element(@sgColorButtonGrey, @sgColorButtonGreyHover, @sgColorButtonGreyText, @sgColorButtonGreyText, transparent, transparent);
}

.sg-btn-virgin-disabled() {
    border: 0;

    .sg-give-horizontal-gradient-to-element(@sgColorButtonDisabledStart, @sgColorButtonDisabledEnd, @sgColorButtonDisabledStart, @sgColorButtonDisabledEnd, @sgColorButtonDisabledText, @sgColorButtonDisabledText, transparent, transparent);
}
