/************ Typography ************/
@sgBaseFontPath				: "../webfonts/";

@sgFontSizeBasePercentage	: 62.5%;	/* Default browser font size is 16px, this percentage makes sure that 1rem equals 10px if default size is unchanged. */
@sgFontSizeRatioXS			: 1;
@sgFontSizeRatioS			: 1;
@sgFontSizeRatioM			: 1;
@sgFontSizeRatioL			: 1;

@sgFontSizeText				: 1.4; 		/* unit = rem */
@sgFontSizeH1				: 1.6; 		/* unit = rem */
@sgFontSizeH2				: 3.6; 		/* unit = rem */
@sgFontSizeH3				: 2.8; 		/* unit = rem */
@sgFontSizeH4               : 2.0;      /* unit = rem */
@sgFontSizeH5               : 1.6;      /* unit = rem */
@sgFontSizeH6               : 1.4;      /* unit = rem */
@sgFontSizeCta              : 1.6;      /* unit = rem */
@sgFontSizeText             : 1.4;      /* unit = rem */
@sgFontSizeTextIntro        : 1.6;      /* unit = rem */
@sgFontSizeTextSmall        : 1.2;      /* unit = rem */
@sgFontSizeLegend			: 1; 		/* unit = rem */
@sgFontSizeInput			: 1.4; 		/* unit = rem */

@sgLineHeightH1				: 1.5;	    /* unitless, it's a multiplier for the calculated font size */
@sgLineHeightH2				: 1.2222;	/* unitless, it's a multiplier for the calculated font size */
@sgLineHeightH3				: 1.3571;   /* unitless, it's a multiplier for the calculated font size */
@sgLineHeightH4             : 1.5;      /* unitless, it's a multiplier for the calculated font size */
@sgLineHeightH5             : 1.5;      /* unitless, it's a multiplier for the calculated font size */
@sgLineHeightH6             : 1.5714;   /* unitless, it's a multiplier for the calculated font size */
@sgLineHeightText           : 1.5714;   /* unitless, it's a multiplier for the calculated font size */
@sgLineHeightTextIntro      : 1.5;      /* unitless, it's a multiplier for the calculated font size */
@sgLineHeightTextSmall      : 1.6667;   /* unitless, it's a multiplier for the calculated font size */
@sgLineHeightLegend			: 1.5;	    /* unitless, it's a multiplier for the calculated font size */
@sgLineHeightLists			: 1.5625;	/* unitless, it's a multiplier for the calculated font size */

@sgFontFamilyDefault		: 'CircularChatBook';
@sgFontFamilyText           : @sgFontFamilyDefault;
@sgFontFamilyHeadings       : 'CircularShoutBold';
@sgFontFamilyH1             : 'CircularChatBook';
@sgFontFamilyH2             : @sgFontFamilyHeadings;
@sgFontFamilyH3             : @sgFontFamilyHeadings;
@sgFontFamilyH4             : @sgFontFamilyHeadings;
@sgFontFamilyH5             : @sgFontFamilyHeadings;
@sgFontFamilyH6             : @sgFontFamilyHeadings;
@sgFontFamilyCta            : 'CircularShoutBold';
@sgFontFamilyTextIntro      : 'CircularShoutBold';
@sgFontFamilyTextSmall      : @sgFontFamilyDefault;
@sgFontFamilyLegend         : @sgFontFamilyDefault;
@sgFontFamilyIcons			: trimm-icons;

@sgTextTransformHeadings	: uppercase;
@sgTextTransformH1			: @sgTextTransformHeadings;
@sgTextTransformH2			: @sgTextTransformHeadings;
@sgTextTransformH3			: @sgTextTransformHeadings;
@sgTextTransformH4          : @sgTextTransformHeadings;
@sgTextTransformH5          : @sgTextTransformHeadings;
@sgTextTransformH6          : @sgTextTransformHeadings;
@sgTextTransformCta         : uppercase;
@sgTextTransformTextIntro   : none;
@sgTextTransformLegend		: none;
