.hpbx-apply-styleguide-specific-styling-header(@styleguide) when (@styleguide = 'upc-business') {
    .hpbx-header-sm() {
        .hpbx-header-logo {
            .remHeight(5.4);
            .remMarginTop(2.4);
        }

        .hpbx-header-background-image {
            background: url("../../dist/img/company-logo-background-@{hpbxStyleguide}.png") no-repeat center top;
            background-color: transparent;
            background-size: contain;
            display: block;
            height: 550px;
            left: 0;
            position: absolute;
            top: 0;
            width: 600px;
            z-index: -2;
        }
    }

    @media @SM {
        .hpbx-header-sm();
    }
}

.hpbx-apply-styleguide-specific-styling-header(@styleguide) when (@styleguide = 'unitymedia') {
    .hpbx-apply-styleguide-specific-styling-header('upc-business');
}

.hpbx-apply-styleguide-specific-styling-header(@styleguide) when (@styleguide = 'ziggo') {
    .hpbx-header-sm() {
        .hpbx-header-logo {
            .remHeight(5.4);
            .remMarginTop(2.4);
        }
    }

    @media @SM {
        .hpbx-header-sm();
    }
}

.hpbx-apply-styleguide-specific-styling-header(@styleguide) when (@styleguide = 'vtr') {
    .hpbx-apply-styleguide-specific-styling-header('ziggo');
}

.hpbx-apply-styleguide-specific-styling-header(@hpbxStyleguide);

.hpbx-header {
    background-color: @sgColorGrey6;
    border-bottom: 1px solid @sgColorGrey5;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1099;

    .box-sizing(content-box);
    .remHeight(4.4);
}

.hpbx-header-logo {
    background: url("../../dist/img/company-logo-small-@{hpbxStyleguide}.png") no-repeat;
    background-size: contain;
    position: absolute;

    .remHeight(2.6);
    .remLeft(1.0);
    .remTop(1);
    .remWidth(12.5);
}

body[data-country="pl"] {
    .hpbx-header-logo {
        background: url("../../dist/img/company-logo-small-@{hpbxStyleguide}-pl.png") no-repeat;
        background-size: contain;
    }
}

.hpbx-header-background-image {
    display: none;
}

.hpbx-header-sm() {
    .hpbx-header {
        background-color: transparent;
        border-bottom: none;
        position: relative;

        .remHeight(8);
        .remWidth(30);

        .hpbx-menu-handle {
            display: none;
        }
    }

    .hpbx-header-logo {
        background: url("../../dist/img/company-logo-big-@{hpbxStyleguide}.png") no-repeat;
        background-size: contain;

        .remHeight(5.4);
        .remMarginLeft(1);
        .remMarginTop(1.5);
        .remWidth(21.9);
    }

    body[data-country="pl"] {
        .hpbx-header-logo {
            background: url("../../dist/img/company-logo-big-@{hpbxStyleguide}-pl.png") no-repeat;
            background-size: contain;
        }
    }
}

@media @SM {
    .hpbx-header-sm();
}

.ie8 {
    .hpbx-header-sm();
}
