input[type="text"],
input[type="email"],
input[type="password"],
textarea {

    &.sg-valid {
        background-color: @sgColorTextfieldBackgroundValid;
        border-color: @sgColorTextfieldBorderValid;
        color: @sgColorTextfieldTextValid;

        &:focus {
            border-color: @sgColorTextfieldBorderValid !important;
        }
    }

    &.sg-error {
        background-color: @sgColorTextfieldBackgroundError;

        &:focus {
            border-color: @sgColorTextfieldBorderErrorHover !important;
        }
    }
}