/*
 Anchor

 Markup: <a href="#" class="{$modifiers}">Link</a>

 .sg-animate.sg-horizontal-lines  - Shows an animation of a horizontal line going up and down on hover
 :focus                           - remove outline

 Styleguide 1.0.1
*/
a {
    color: @sgColorAnchor;
    text-decoration: underline;

	.transition(color @sgMiscDurationTransitionDefault);

    &:focus {
        outline-style: none;
    }

    &:hover {
        color: @sgColorAnchorHover;
    }

    &.sg-animate {
        display: inline-block;
        position: relative;

        &.sg-horizontal-lines {
            
            text-decoration: none;

            .remPadding(0.2, 0, 0.1, 0);

            &:before,
            &:after {
                background: @sgColorAnchor;
                content: '';
                left: 0;
                position: absolute;
                top: 100%;
                width: 100%;

                .remHeight(0.1);
                .transition-transform(@sgMiscDurationTransitionDefault);
                .transform(scale(0.85));
            }

            &:after {
                .opacity(0);
                .transition(all @sgMiscDurationTransitionDefault);
            }

            &:hover:before,
            &:hover:after,
            &:focus:before,
            &:focus:after {
                background: @sgColorAnchorHover;

				.transform(scale(1));
			}

			&:hover:after,
			&:focus:after {
				top: 0%;
				
				.opacity(1);
			}
		}
	}
}
