/* This mixin sets (gradient) colors on an HTML element. */
.sg-give-horizontal-gradient-to-element(@backgroundColorStart, @backgroundColorEnd, @backgroundColorHoverStart, @backgroundColorHoverEnd, @color, @colorHover, @colorBorder, @colorBorderHover) {
    background-color: @backgroundColorStart;
    color: @color;

    .horizontal(@backgroundColorStart, @backgroundColorEnd);

    &:hover {

        background-color: @backgroundColorEnd;
        color: @colorHover;

        .horizontal(@backgroundColorHoverStart, @backgroundColorHoverEnd);
    }
}